import $t from './tool.js'

export default{
    /**
     * token
     * @param {String} key 
     */
    getToken(key){
        $t.getCookie(key)
    },
    /**
     * token
     * @param {String} tokenKey 
     * @param {String} token 
     */
    setToken(tokenKey,token){
        $t.setCookie(tokenKey,token,7)
    },
    /**
     * token
     * @param {String} tokenKey 
     */
    removeToken(tokenKey){
        $t.removeCookie(tokenKey)
    }
}


