<template>
    <div class="box">
        <title></title>
        <Form ref="formValidate" :model="formValidate" :rules="ruleValidate" :label-width="80">
            <FormItem label=":" prop="name" :label-width="120">
                <Input v-model="formValidate.name" placeholder="Enter your name"/>
            </FormItem>
            <FormItem label=":" prop="gender" :label-width="120">
                <RadioGroup v-model="formValidate.gender">
                    <Radio label="male"></Radio>
                    <Radio label="female"></Radio>
                </RadioGroup>
            </FormItem>
            <FormItem label="" prop="interest" :label-width="120">
                <CheckboxGroup v-model="formValidate.interest">
                    <Checkbox label="Eat"></Checkbox><br/>
                    <Checkbox label="Sleep"></Checkbox><br/>
                    <Checkbox label="Run"></Checkbox><br/>
                    <Checkbox label="Movie"></Checkbox><br/>
                </CheckboxGroup>
            </FormItem>

        </Form>
    </div>
</template>
<script>
export default {
    data(){
        return{
            formValidate:{
                name:'',
                gender: '',
                interest: [],

            },
            ruleValidate:{
                name: [
                    { required: true, message: 'The name cannot be empty', trigger: 'blur' }
                ],
                gender: [
                    { required: true, message: 'Please select gender', trigger: 'change' }
                ],
                interest: [
                    { required: true, type: 'array', min: 1, message: 'Choose at least one hobby', trigger: 'change' },
                    { type: 'array', max: 2, message: 'Choose two hobbies at best', trigger: 'change' }
                ],

            }
        }
    }
}
</script>


