import axios from 'axios';
import store from '../store'
import $auth from '@/utils/auth'

// axios
let service =  axios.create({
    baseURL: process.env.BASE_API, // apibase_url
    // timeout: 5000 // 
  })

  // request
service.interceptors.request.use(config => {
    if (store.getters.token) {
      config.headers = {
        'Authorization' : "Token " + $auth.getToken('Token'), //
       };
    }
    return config
  }, error => {
    Promise.reject(error)
  })

  // request
service.interceptors.request.use(config => {
    if (store.getters.token) {
      config.headers = {
        'Authorization' : "Token " + $auth.getToken('Token'), //
       };
    }
    return config
  }, error => {
    Promise.reject(error)
  })
  
  // respone
  service.interceptors.response.use(
    response => {
     /**
      * code:200,;
      */
      const res = response.data
      
      if (res.code !== 200) {
        // Message({
        //   message: res.message,
        //   type: 'error',
        //   duration: 5 * 1000
        // })
        // 5001:token; 5002:; 5004:Token ;
        if (res.code === 5001 || res.code === 5002 || res.code === 5004) {
            // MessageBox.confirm('', '', {
            //   confirmButtonText: '',
            //   cancelButtonText: '',
            //   type: 'warning'
            // }).then(() => {
            //   store.dispatch('LogOut').then(() => {
            //     location.reload()// vue-router bug
            //   })
            // })
        }
        return Promise.reject('error')
      } else { // res.code === 200,
          return response.data
      }
    },
    error => {
      // Message({
      //   message: error.message,
      //   type: 'error',
      //   duration: 5 * 1000
      // })
      return Promise.reject(error)
    }
  )

  export default service

  


